/* eslint-disable */
// CSS
import "../sass/index.sass";
// JS
import Stats from "stats.js";
require('aframe-orbit-controls');
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
import { OutputPass } from "three/examples/jsm/postprocessing/OutputPass";
import { GTAOPass } from "three/examples/jsm/postprocessing/GTAOPass";
// import { SSAOPass } from "three/examples/jsm/postprocessing/SSAOPass";
// import { DotScreenPass } from "three/examples/jsm/postprocessing/DotScreenPass";
// import { BokehPass } from 'three/examples/jsm/postprocessing/BokehPass';

//網頁資料下載完成
document.addEventListener('DOMContentLoaded', function() {
  const sceneEl = document.querySelector('a-scene');
  sceneEl.setAttribute('load-complete', '');
});

// Stats
const stats = new Stats();
const statsContainer = document.getElementById('stats-container');
statsContainer.appendChild(stats.dom);

//3D資源下載完成
AFRAME.registerComponent('load-complete', {
    init: function() {
      // SCENE
      const sceneEl = document.querySelector('#scene');

      new RGBELoader().load("images/studio2k.hdr", function(texture){
        texture.mapping = THREE.EquirectangularReflectionMapping;
        texture.image.data = texture.image.data.map(value => value * 0.93);
				sceneEl.object3D.environment = texture;
      });
      
      // LIGHT
      const basicLight = document.querySelector('#basic_light');

      // CAMERA
      const basicCamera = document.querySelector('#basic_camera');

      // Bloom
      const renderScene = new RenderPass( sceneEl.object3D, sceneEl.camera );
      // console.log(window.innerWidth + " , " + window.innerHeight);
      const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
			bloomPass.threshold = 1;
			bloomPass.strength = 0.33;
			bloomPass.radius = 0;

      const outputPass = new OutputPass();
      const renderer = sceneEl.renderer;
      renderer.setSize(window.innerWidth, window.innerHeight);
      const render = renderer.render;

      // const bokehPass = new BokehPass( sceneEl.object3D, sceneEl.camera, {
      //   focus: 1,
      //   aperture: 1,
      //   maxblur: 0.01
      // } );

      // const dotScreenPass = new DotScreenPass();

      const gtaoPass = new GTAOPass( sceneEl.object3D, sceneEl.camera, window.innerWidth, window.innerHeight );
      gtaoPass.output = GTAOPass.OUTPUT.Default;
      gtaoPass.thickness = 10;
      gtaoPass.scale = 1;

      // const ssaoPass = new SSAOPass( sceneEl.object3D, sceneEl.camera, window.innerWidth, window.innerHeight );
      // ssaoPass.output = SSAOPass.OUTPUT.Default;
      // ssaoPass.kernelRadius = 128;
      // ssaoPass.minDistance = 0.001;
      // ssaoPass.maxDistance = 0.3;

      const composer = new EffectComposer( renderer );
      composer.addPass( renderScene );
      composer.addPass( bloomPass );
      composer.addPass( gtaoPass );
      // composer.addPass( ssaoPass );
      // composer.addPass( dotScreenPass );
      // composer.addPass( bokehPass );
      composer.addPass( outputPass );

      let isDigest = false;
      renderer.render = function () {
        if (isDigest) {
          render.apply(renderer, arguments);
        } else {
          isDigest = true;
          composer.render();
          isDigest = false;
        }
      };

/*
      let iphone, cup;
      setTimeout(function(){
        // console.log(cart.children[0].children[0]);
        let basic_object = getObjectByName(cart.children[0],"basic");
        console.log(basic_object);
      },3000);

      function getObjectByName(object3D,namedata){
        let objectData;
        object3D.children.forEach(function (item) {
          if (item.name === namedata) {
            objectData = item;
          }
        });
        return objectData;
      }
       */ 
      
      // 在 requestAnimationFrame 中循環更新
      function updatePosition() {
        // STATS
        stats.update();
        // 循環更新
        requestAnimationFrame(updatePosition);
      }
      updatePosition();
    }
});

AFRAME.registerComponent('cursor-listener', {
  init: function () {
    // cart
    
    const basic = document.querySelector('#basic');
    const basic_bar = document.querySelector('#basic_bar');
    let basic_bar_goy = 0;
    let basic_bar_goz = 0;

    const back_bar = document.querySelector('#back_bar');
    const ubk = document.querySelector('#ubk');
    const umbrella = document.querySelector('#umbrella');
    let umbrella_goy = 0;

    const box1 = document.querySelector('#box1');
    const box1_close = document.querySelector('#box1_close');
    const box1_open = document.querySelector('#box1_open');

    const box2 = document.querySelector('#box2');
    const box2_open = document.querySelector('#box2_open');
    let box2_open_goz = 0;

    const box3 = document.querySelector('#box3');
    const box3_open = document.querySelector('#box3_open');
    let box3_open_goz = 0;

    const cup_base = document.querySelector('#cup_base');
    const cup = document.querySelector('#cup');
    const cup_base_bottom = document.querySelector('#cup_base_bottom');
    const cup_base_middle = document.querySelector('#cup_base_middle');
    let cup_goy = 0;
    let cup_base_bottom_gorx = 0;
    let cup_base_middle_gorx = 0;

    const power = document.querySelector('#power');
    const iphone = document.querySelector('#iphone');
    let iphone_goy = 0;

    const wheel_bar = document.querySelector('#wheel_bar');
    const wheel = document.querySelector('#wheel');
    const wheel33 = document.querySelector('#wheel33');

    
    this.el.addEventListener('click', function (evt) {
      if(evt.target == back_bar || evt.target == ubk || evt.target == umbrella){
        if(umbrella.object3D.position.y != 0){
          umbrella_goy = 0;
        }else{
          umbrella.object3D.position.y = 0.011;
          umbrella_goy = 1;
        }
      }else if(evt.target == basic_bar){
        if(basic_bar.object3D.position.y != 0){
          basic_bar_goy = 0;
          basic_bar_goz = 0;
        }else{
          basic_bar.object3D.position.y = -0.0011;
          basic_bar_goy = -0.037928;
          basic_bar_goz = 0.03258;
        }
      }else if(evt.target == box1_close || evt.target == box1_open){
        if(box1_close.object3D.visible){
          box1_close.object3D.visible = false;
          box1_open.object3D.visible = true;
        }else{
          box1_close.object3D.visible = true;
          box1_open.object3D.visible = false;
        }
      }else if(evt.target == box2_open){
        if(box2_open.object3D.position.z != 0){ 
          box2_open_goz = 0; 
        }else{ 
          box2_open.object3D.position.z = 0.011;
          box2_open_goz = 0.1; 
        }
      }else if(evt.target == box3_open){
        if(box3_open.object3D.position.z != 0){ 
          box3_open_goz = 0; 
        }else{ 
          box3_open.object3D.position.z = 0.011;
          box3_open_goz = 0.1; 
        }
      }else if(evt.target == cup_base || evt.target == cup || evt.target == cup_base_bottom || evt.target == cup_base_middle){
        if(cup.object3D.position.y != 0){
          cup_goy = 0;
          cup_base_bottom_gorx = 0;
          cup_base_middle_gorx = 0;
        }else{
          cup.object3D.position.y = 0.011;
          cup_base_bottom.object3D.rotation.x = 0.011;
          cup_base_middle.object3D.rotation.x = -0.011;
          cup_goy = 1;
          cup_base_bottom_gorx = Math.PI/2;
          cup_base_middle_gorx = -Math.PI/2;
        }
        
      }else if(evt.target == power || evt.target == iphone){
        if(iphone.object3D.position.y != 0){
          iphone_goy = 0;
        }else{
          iphone.object3D.position.y = 0.01;
          iphone_goy = 1;
        }
      }else if(evt.target == wheel_bar || evt.target == wheel || evt.target == wheel33){
        if(wheel.object3D.visible){
          wheel.object3D.visible = false;
          wheel33.object3D.visible = true;
        }else{
          wheel.object3D.visible = true;
          wheel33.object3D.visible = false;
        }
      }
    });

    function updateAnimation() {

      if(basic_bar.object3D.position.y > -0.001){
        basic_bar.object3D.position.y = 0;
        basic_bar.object3D.position.z = 0;
      }else{
        basic_bar.object3D.position.y = basic_bar.object3D.position.y + (basic_bar_goy - basic_bar.object3D.position.y)/6;
        basic_bar.object3D.position.z = basic_bar.object3D.position.z + (basic_bar_goz - basic_bar.object3D.position.z)/6;
      }

      if(umbrella.object3D.position.y < 0.01){
        umbrella.object3D.position.y = 0;
      }else{
        umbrella.object3D.position.y = umbrella.object3D.position.y + (umbrella_goy - umbrella.object3D.position.y)/6;
      }
      if(umbrella.object3D.position.y > 0.8){umbrella.object3D.visible = false;}
      else{umbrella.object3D.visible = true;}

      
      if(box2_open.object3D.position.z < 0.01){
        box2_open.object3D.position.z = 0;
      }else{
        box2_open.object3D.position.z = box2_open.object3D.position.z + (box2_open_goz - box2_open.object3D.position.z)/6;
      }

      if(box3_open.object3D.position.z < 0.01){
        box3_open.object3D.position.z = 0;
      }else{
        box3_open.object3D.position.z = box3_open.object3D.position.z + (box3_open_goz - box3_open.object3D.position.z)/6;
      }

      
      if(iphone.object3D.position.y < 0.01){
        iphone.object3D.position.y = 0;
      }else{
        iphone.object3D.position.y = iphone.object3D.position.y + (iphone_goy - iphone.object3D.position.y)/6;
      }
      if(iphone.object3D.position.y > 0.8){
        iphone.object3D.visible = false;
      }else{
        iphone.object3D.visible = true;
      }

      
      if(cup.object3D.position.y < 0.01){
        cup.object3D.position.y = 0;
      }else{
        cup.object3D.position.y = cup.object3D.position.y + (cup_goy - cup.object3D.position.y)/6;
      }
      
      if(cup_base_bottom.object3D.rotation.x < 0.01){
        cup_base_bottom.object3D.rotation.x = 0;
      }else{
        cup_base_bottom.object3D.rotation.x = cup_base_bottom.object3D.rotation.x + (cup_base_bottom_gorx - cup_base_bottom.object3D.rotation.x)/6;
      }
      
      if(cup_base_middle.object3D.rotation.x > -0.01){
        cup_base_middle.object3D.rotation.x = 0;
      }else{
        cup_base_middle.object3D.rotation.x = cup_base_middle.object3D.rotation.x + (cup_base_middle_gorx - cup_base_middle.object3D.rotation.x)/6;
      }
      if(cup.object3D.position.y > 0.8){
        cup.object3D.visible = false;
      }else{
        cup.object3D.visible = true;
      }

      // 循環更新
      requestAnimationFrame(updateAnimation);
    }
    updateAnimation();
  }
});